html, body {
  width: 100%;
  height: 100%;
  font-family: "HKGroteskRegular", Roboto, sans-serif !important;
  scroll-behavior: smooth;
}

body {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
}

#profile {
  width: 100vw;
  margin: 0 auto;
